<template>
  <div
    :class="{
      'portal-add-popup': true,
      [checkBoxClass]: true,
      'portal-add-popup--no-pagination': !hasPagination,
    }"
  >
    <el-dialog
      class="search"
      :title="addForm.title"
      :visible.sync="productVisible"
      width="800px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <div class="topTitle">{{ $t('IDC.orgCustomer.selectedProduct') }}</div>
      <div class="selected-container">
        <div class="show-selected-div">
          <el-tag
            type="info"
            v-for="(item, index) in getObjFlatValue(uncommitSelectionache, addForm.idKey)"
            :key="index"
            :closable="true"
            @close="removeSelected(item)"
          >
            {{ item.productName }}
          </el-tag>
        </div>
      </div>
      <table-page
        ref="data-table"
        size="small"
        idKey="productId"
        :is-check="isCheck"
        :is-check-memory="true"
        :is-search="refreshTableFlag"
        :has-pagination="hasPagination"
        :columns="addForm.columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :init-checks="multipleChecks"
        :id-key="addForm.idKey"
        @fetchData="fetchData"
        @selection-change="onSelectionChange"
        :expand-row-keys="expands"
        :sortArr="['productName', 'productDesc']"
      >
      </table-page>
      <div slot="footer">
        <el-button id="cancel" @click="cancel" size="small">{{ $t('common.button.cancel') }} </el-button>
        <el-button id="confirm" @click="submit" size="small" v-preventReClick type="primary">
          {{ $t('common.button.confirm') }}
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';

export default {
  name: 'addPrompt',
  components: { TablePage },
  props: {
    productVisible: {
      type: Boolean,
      default: false,
    },
    hasCheckBox: {
      type: Boolean,
      default: true,
    },
    hasPagination: {
      type: Boolean,
      default: false,
    },
    isCheck: {
      type: Boolean,
      default: false,
    },
    isCheckMemory: {
      type: Boolean,
      default: false,
    },
    isShowCloseBtn: {
      type: Boolean,
      default: false,
    },
    tableData: {
      type: Array,
      default: () => [],
    },
    initChecks: {
      type: Array,
      default: () => [],
    },
    options: {
      type: Object,
      default: () => ({}),
    },
    addForm: {
      type: Object,
      default: () => {},
    },
    submitCallBack: {
      type: String,
      default: () => '',
    },
    childSearch: {
      type: Function,
      default: null,
    },
    smallAutoColumn: {
      type: Number,
      default: 3,
      validate: (value) => {
        return !(24 % value);
      },
    },
    bigAutoColumn: {
      type: Number,
      default: 4,
      validate: (value) => {
        return !(24 % value);
      },
    },
  },
  watch: {
    productVisible(val) {
      if (val) {
        this.refreshTableFlag = true;
        this.multipleSelection = this.getCheckedSelection();
      }
      this.firstLoadTableEnd = false;
    },
  },
  data() {
    return {
      expands: [],
      paginationConfig: {
        total: (this.addForm && this.addForm.columns && this.addForm.columns.length) || 0,
        pageIndex: 1,
        pageSize: 5,
      },
      multipleSelection: [],
      multipleSelectionCache: {},
      uncommitSelectionache: {},
      refreshTableFlag: false,
      searchLoading: false,
      param: {},
      multipleChecks: this.initChecks,
      initChecksCache: this.initChecks,
      firstLoadTableEnd: false,
    };
  },
  computed: {
    formConfigList() {
      return this.addForm && this.addForm.searchForm && this.addForm.searchForm.formConf;
    },
    formData() {
      return this.addForm && this.addForm.searchForm && this.addForm.searchForm.formData;
    },
    checkBoxClass() {
      const hasCheckBox = this.hasCheckBox;
      if (hasCheckBox) {
        return 'checkbox-on';
      }
      return 'checkbox-off';
    },
  },
  methods: {
    getCheckedSelection() {
      const idKey = this.addForm.idKey || 'id';
      const ids = this.initChecks.map((item) => item[idKey]);
      const rows = this.getObjFlatValue(this.multipleSelectionCache, idKey);
      if (!rows) return [];
      return rows.filter((row) => ids.includes(row[idKey]));
    },
    fetchData(data) {
      this.paginationConfig.total = data.total;
      this.searchLoading = false;
      this.$emit('data-fetcher', data);
    },
    filterListByField(list, field) {
      return list.reduce((pre, next) => {
        if (!pre.length) {
          return [...pre, next];
        }
        const hasVal = pre.some((row) => row[field] === next[field]);
        if (hasVal) {
          return pre;
        }
        return [...pre, next];
      }, []);
    },
    getObjFlatValue(obj, idKey) {
      let arr = Object.values(obj);
      arr = arr.reduce((pre, next) => {
        if (!pre.length) {
          return [...next];
        }
        return [...pre, ...next];
      }, []);
      arr = this.filterListByField(arr, idKey);
      return arr;
    },
    onSelectionChange(val) {
      const idKey = this.addForm.idKey;
      const curPageNum = this.paginationConfig.pageIndex;
      let initChecksCache = this.initChecksCache;
      let curPageSelection = this.multipleSelectionCache[curPageNum] || [];
      // 当前 selection idKey list
      const ids = val.map((item) => item[idKey]);
      // 需要从缓存当前页的selection中删除项
      const deletedRows = curPageSelection.filter((item) => !ids.includes(item[idKey]));
      const deletedRowsIds = deletedRows.map((item) => item[idKey]);
      // 把 deletedRows 从 initChecksCache 中移除
      initChecksCache = initChecksCache.filter((item) => !deletedRowsIds.includes(item[idKey]));

      this.initChecksCache = [...initChecksCache];
      this.multipleSelectionCache[curPageNum] = [...val];
      // 把 multipleSelectionCache 转化成数组 multipleSelection，待submit提交使用
      // {1:[{a:'a'}],2:[{b:'b'}]} => [{a:'a'},{b:'b'}]
      this.multipleSelection = this.getObjFlatValue(this.multipleSelectionCache, idKey);
      // 未提交选中项，切页缓存
      this.uncommitSelectionache = { ...this.multipleSelectionCache };
    },
    async getTableData(param) {
      let result = null;
      const idKey = this.addForm.idKey;
      if (this.tableData.length > 0) {
        result = this.tableData;
      } else {
        if (this.param.searchKey) {
          param.searchKey = this.param.searchKey;
        }
        result = await this.childSearch(param);
      }
      // 未提交选中项数组，跟 selection 数据结构一致
      const uncommitSelectionacheArr = this.getObjFlatValue(this.uncommitSelectionache, idKey);
      // multipleChecks
      const initChecksCache = this.firstLoadTableEnd ? this.initChecksCache : this.initChecks;
      this.firstLoadTableEnd = true;
      const allCheckedSelection = [...initChecksCache, ...uncommitSelectionacheArr];
      const checkedIds = allCheckedSelection.map((check) => check[idKey]);
      const resList = result.list || [];
      // 判断tableList 是否包含选择项
      const hasInterSection = resList.some((row) => checkedIds.includes(row[idKey]));
      let multipleChecks = [];
      if (hasInterSection) {
        multipleChecks = allCheckedSelection;
      }
      // 等待空闲时间渲染checkbox选中
      window.requestIdleCallback(() => {
        this.multipleChecks = multipleChecks;
      });

      this.refreshTableFlag = false;
      return result;
    },
    submit() {
      const options = this.options;
      this.$emit('submit-call-back', this.multipleSelection, options);
      this.$emit('update:productVisible', false);
      this.multipleChecks = [];
    },
    cancel() {
      this.$emit('update:productVisible', false);
      this.multipleChecks = [];
    },
    handleSelect(scope) {
      // 表格显示下拉控制
      let productId = scope.data.row.productId;
      if (this.expands.indexOf(productId) === -1) {
        this.expands.push(productId);
      } else {
        this.expands.splice(this.expands.indexOf(productId), 1);
      }
    },
    removeSelected(record) {
      this.$refs['data-table'].$refs.tableRef.toggleRowSelection(record);
    },
  },
  filters: {
    obj2str(val) {
      const arr = [];
      arr.push(Object.values(val)[1]);
      return arr.join(',');
    },
  },
  created() {},
  mounted() {},
  activated() {},
};
</script>

