<template>
  <div class="enter-details-pages" :data="dataObj" border style="width: 100%" v-loading="detaileLoading">
    <el-form :model="dataObj" ref="detailForm" :rules="detailRules">
      <div class="pro-wrp">
        <!-- Trust Level and Segment -->
        <div class="content-wrapper">
          <el-form-item class="tip">
            <span class="enterTitle">Trust Level and Segment</span>
          </el-form-item>
        </div>
        <el-divider />
        <el-row class="detail detail-tl" :gutter="20">
          <el-col v-bind="colOptions" class="trustLevel">
            <el-form-item label="Trust Level">
              <span v-text="`${dataObj.trustLevel || '-'}:${dataObj.trustLevelName || '-'}`" />
            </el-form-item>
          </el-col>
          <el-col v-bind="colOptions" class="trustLevel">
            <el-form-item label="Segment">
              <span v-text="`${dataObj.selectSegmentName || '-'}`" />
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <div class="segmentation" v-if="crIndex === 1"></div>
      <div class="pro-wrp select-wrp">
        <!-- Select Product -->
        <div v-if="crIndex === 1">
          <div class="tip1">
            <span class="enterTitle">{{ $t('IDC.orgCustomer.selectProduct') }}</span>
            <div class="basicDec1">
              {{ $t('IDC.orgCustomer.selectProductDic') }}
            </div>
          </div>
        </div>
        <el-divider v-if="crIndex === 1" />
        <div class="content-wrapper" v-if="crIndex === 1">
          <el-form-item class="select-product-css">
            <el-button icon="el-icon-plus" plain size="small" type="primary" @click="productAddEvn"
              >{{ $t('common.button.add') }}
            </el-button>
          </el-form-item>
          <el-form-item>
            <table-page
              :columns="selectProductColums"
              :has-pagination="true"
              :outSideTableData="dataObj.productInfoList"
            >
              <template slot="effectiveDate" slot-scope="scope">
                <el-form-item
                  :prop="`productInfoList.${scope.data.$index}.effectiveDate`"
                  :rules="{
                    required: true,
                    trigger: ['blur', 'change'],
                    validator: getEffectiveDateRange(scope.data.row.expireDate),
                  }"
                >
                  <el-date-picker
                    v-model="scope.data.row.effectiveDate"
                    type="date"
                    size="small"
                    :placeholder="$t('common.placeholder.enter')"
                    style="width: 100%"
                    format="dd/MM/yyyy"
                    value-format="dd/MM/yyyy"
                    :picker-options="
                      pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                    "
                  ></el-date-picker>
                </el-form-item>
              </template>
              <template slot="expireDate" slot-scope="scope">
                <el-form-item
                  :prop="`productInfoList.${scope.data.$index}.expireDate`"
                  :rules="{
                    required: true,
                    trigger: ['blur', 'change'],
                    validator: getExpireDateRange(scope.data.row.effectiveDate),
                  }"
                >
                  <el-date-picker
                    v-model="scope.data.row.expireDate"
                    format="dd/MM/yyyy"
                    value-format="dd/MM/yyyy"
                    type="date"
                    size="small"
                    :placeholder="$t('common.placeholder.enter')"
                    style="width: 100%"
                    :picker-options="
                      pickerOptionsEffectiveDate(scope.data.row.effectiveDateInit, scope.data.row.expireDateInit)
                    "
                  ></el-date-picker>
                </el-form-item>
              </template>
              <template slot="chargeProfileId" slot-scope="scope">
                <el-select v-model="scope.data.row.chargeProfileId" placeholder="Please select">
                  <el-option
                    v-for="item in scope.data.row.chargeProfiles"
                    :key="item.chargeProfileId"
                    :label="item.chargeProfileName"
                    :value="item.chargeProfileId"
                  >
                  </el-option>
                </el-select>
              </template>
              <template slot="operation" slot-scope="scope">
                <el-button
                  size="mini"
                  type="text"
                  v-if="scope.data.row.isProduct > 0"
                  @click="handleDelete(scope.data.$index)"
                  >{{ 'Delete' }}</el-button
                >
              </template>
            </table-page>
          </el-form-item>
        </div>
      </div>

      <div class="segmentation"></div>
      <div class="pro-wrp">
        <!-- Profile -->
        <div class="content-wrapper">
          <el-form-item class="tip">
            <span class="enterTitle">{{ $t('IDC.orgCustomer.profile') }}</span>
          </el-form-item>
        </div>
        <el-divider />
        <div class="content-wrapper">
          <el-row class="detail" :gutter="20">
            <el-col v-bind="colOptions">
              <el-form-item class="identity" :label="$t('IDC.orgCustomer.chargeProfile')" prop="chargeProfileobj">
                <el-select
                  :value="dataObj.chargeProfileobj.chargeProfileId"
                  placeholder="Please select"
                  clearable
                  @change="seletedChargeProfile"
                >
                  <el-option
                    v-for="item in dataObj.selectChargeProfileList"
                    :key="item.chargeProfileId"
                    :label="item.chargeProfileName"
                    :value="item.chargeProfileId"
                  >
                  </el-option>
                </el-select>
                <div class="identity-btn" @click="chargeProfileUseDefault">{{ $t('IDC.orgCustomer.useDefault') }}</div>
              </el-form-item>
            </el-col>
            <el-col v-bind="colOptions" class="identity">
              <el-form-item :label="$t('IDC.orgCustomer.ruleProfile')" prop="ruleProfileobj" required>
                <el-select
                  :value="dataObj.ruleProfileobj.ruleProfileId"
                  placeholder="Please select"
                  @change="seletedRuleProfile"
                >
                  <el-option
                    v-for="item in dataObj.selectRuleProfileList"
                    :key="item.ruleProfileId"
                    :label="item.ruleProfileName"
                    :value="item.ruleProfileId"
                  >
                  </el-option>
                </el-select>
                <div class="identity-btn" @click="ruleProfileUseDefault">{{ $t('IDC.orgCustomer.useDefault') }}</div>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="segmentation"></div>
      <div class="pro-wrp">
        <!-- Account -->
        <div class="content-wrapper">
          <el-form-item class="tip">
            <span class="enterTitle">{{ $t('IDC.orgCustomer.account') }}</span>
            <div class="basicDec">
              {{ $t('IDC.orgCustomer.accountDic') }}
            </div>
          </el-form-item>
        </div>
        <el-divider />
        <div class="content-wrapper">
          <el-form-item>
            <table-page :columns="accountConfig" :has-pagination="false" :outSideTableData="dataObj.accountTypeList">
              <template slot="ruleid" slot-scope="scope">
                <el-select
                  v-model="scope.data.row.ruleProfileId"
                  placeholder="Please select"
                  v-if="!scope.data.row.accountType.limitExclude"
                >
                  <el-option
                    v-for="item in scope.data.row.ruleProfileBeans"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
                <span v-else> -- </span>
              </template>
              <template slot="operation" slot-scope="scope">
                <el-button
                  size="mini"
                  type="text"
                  @click="handleUseDefault(scope.data)"
                  v-if="!scope.data.row.accountType.limitExclude"
                  >{{ $t('IDC.orgCustomer.useDefault') }}</el-button
                >
                <span v-else> -- </span>
              </template>
            </table-page>
          </el-form-item>
        </div>
      </div>
      <!-- 多选弹框 -->
      <product-dialog
        :productVisible.sync="productVisible"
        :addForm="productForm"
        :isCheck="true"
        :isCheckMemory="true"
        :hasPagination="true"
        :childSearch="loadProductParam"
        @data-fetcher="fetchProductData"
        @submit-call-back="onProductChange"
      ></product-dialog>
    </el-form>
  </div>
</template>
<script>
import TablePage from '@/components/tablePage';
import productDialog from './product-dialog';
import { queryAssignProduct } from '../../../../api/api-customer';
import { selectRequiredValidateObj } from '@/common/validator/common';
import dayjs from 'dayjs';
export default {
  name: 'CustomerDetails',
  components: { TablePage, productDialog },
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
    crIndex: {
      type: Number,
      default: 0,
    },
    detaileLoading: {
      type: Boolean,
      default: false,
    },
  },
  provide() {
    return {};
  },
  data() {
    return {
      labelStyle: {
        'font-size': '14px !important',
        color: '#86909c !important',
        'font-weight': '400 !important',
        padding: '0 10px 10px 10px !important',
        'font-family': 'Huawei Sans',
      },
      contentStyle: {
        'font-size': '14px !important',
        color: `${this.GlobalStyleVariables.fillEmphasisColor} !important`,
        'font-weight': '400 !important',
        padding: '0 10px 25px 10px !important',
        'font-family': 'Huawei Sans',
      },
      selectProductColums: [
        { label: this.$t('IDC.orgCustomer.productName'), prop: 'productName', align: 'left' },
        {
          label: this.$t('IDC.orgCustomer.EffectiveDate'),
          prop: 'effectiveDate',
          align: 'left',
          __slotName: 'effectiveDate',
        },
        {
          label: this.$t('IDC.orgCustomer.expirationDate'),
          prop: 'expireDate',
          align: 'left',
          __slotName: 'expireDate',
        },
        {
          label: this.$t('IDC.orgCustomer.chargeProfile'),
          prop: 'chargeProfileId',
          align: 'left',
          __slotName: 'chargeProfileId',
        },
        { label: this.$t('IDC.orgCustomer.description'), prop: 'productDesc', align: 'left' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        },
      ],
      isAccountConfig: true, // accountColumns判断条件
      accountColumnsRule: [
        { label: 'Account Type', prop: 'accountType.accountTypeName', align: 'left' },
        { label: 'Currency', prop: 'currency', align: 'left' },
        {
          label: 'Rule Profile',
          prop: 'ruleid',
          align: 'left',
          __slotName: 'ruleid',
        },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
        },
      ],
      accountColumns: [
        { label: this.$t('common.label.accountType'), prop: 'accountType', align: 'left' },
        { label: this.$t('common.label.currency'), prop: 'accountType', align: 'left' },
        { label: this.$t('common.label.ruleProfile'), prop: '-', align: 'left' },
      ],
      productVisible: false, // 弹框
      productForm: {
        title: this.$t('IDC.orgCustomer.addProduct'),
        idKey: 'productId',
        columns: [
          {
            label: this.$t('IDC.orgCustomer.productName'),
            prop: 'productName',
            'sort-method': (a, b) => {
              return a.productName > b.productName ? 1 : -1;
            },
            align: 'left',
          },
          {
            label: this.$t('IDC.orgCustomer.description'),
            prop: 'productDesc',
            'sort-method': (a, b) => {
              return a.productDesc > b.productDesc ? 1 : -1;
            },
            align: 'left',
          },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: this.$t('operatorRoleCreate.productName'),
              key: 'productName',
              prop: 'productName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
            {
              label: this.$t('operatorRoleCreate.serviceName'),
              key: 'serviceName',
              prop: 'serviceName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      pickerOptions: {
        effectiveDate: {
          disabledDate: (time) => {
            let endDateVal = this.ciContent.expireDate;
            if (endDateVal) {
              return time.getTime() > new Date(endDateVal).getTime() - 24 * 60 * 60 * 1000;
            }
          },
        },
        expireDate: {
          disabledDate: (time) => {
            let beginDateVal = this.ciContent.effectiveDate;
            if (beginDateVal) {
              return time.getTime() < new Date(beginDateVal).getTime() + 24 * 60 * 60 * 1000;
            }
          },
        },
      },
      loading: null,
      detailRules: { ruleProfileobj: [selectRequiredValidateObj], chargeProfileobj: [] },
    };
  },
  computed: {
    accountConfig() {
      return this.isAccountConfig ? this.accountColumnsRule : this.accountColumns;
    },
  },
  methods: {
    validate() {
      return new Promise((resolve) => {
        this.$refs['detailForm'].validate((isValid) => {
          resolve(isValid);
        });
      });
    },
    profileUseDefault() {},
    handleDelete(index) {
      this.$confirm('Are you sure to delete it?', '', {
        confirmButtonText: this.$t('common.button.delete'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'warning',
        customClass: 'delete-confirm',
      }).then(() => {
        this.dataObj.productInfoList.splice(index, 1);
        this.$emit('refresh-accountType', {});
      });
    },
    handleUseDefault(data) {
      let obj = data.row;
      obj.ruleProfileId = data.row.defaultRuleProfileId;
    },
    productAddEvn() {
      this.productVisible = true;
    },
    loadProductParam(param) {
      return new Promise((resolve) => {
        let products = [];
        this.dataObj.productInfoList.forEach((item) => {
          products.push(item.productId);
        });
        let p = {
          page: param.pageNo || 1,
          productName: param.searchKey,
          selectProducts: products,
          trustLevelId: this.dataObj.trustLevelId,
          segmentIds: this.dataObj.segmentIds,
        };
        queryAssignProduct(p)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products.map((item) => {
                item.effectiveDateInit = item.effectiveDate;
                item.expireDateInit = item.expireDate;
                return { ...item };
              });
            }
            resolve(res);
          })
          .catch(() => {
            this.searchLoading = false;
          })
          .finally(() => {
            this.searchLoading = false;
            this.isSearch = false;
          });
      });
    },
    fetchProductData(data) {
      this.productTableData = data.list;
    },
    // Add Product 弹窗
    onProductChange(data) {
      data.forEach((item) => {
        let obj = Object.assign({}, item);
        obj.isProduct = 1;
        this.dataObj.productInfoList.push(obj);
      });
      if (data.length > 0) {
        this.$emit('refresh-accountType', {});
      }
    },
    chargeProfileUseDefault() {
      this.$emit('charge-profile-usedefault', {});
    },
    ruleProfileUseDefault() {
      this.$emit('rule-profile-usedefault', {});
    },
    seletedChargeProfile(param) {
      this.dataObj.chargeProfileobj = {
        ...this.dataObj.selectChargeProfileList.filter((item) => item.chargeProfileId == param)[0],
      };
    },
    seletedRuleProfile(param) {
      this.dataObj.ruleProfileobj = {
        ...this.dataObj.selectRuleProfileList.filter((item) => item.ruleProfileId == param)[0],
      };
    },
    pickerOptionsEffectiveDate(data, data2 = '') {
      return {
        disabledDate: (time) => {
          let startDateVal = data;
          let endDateVal = data2;
          if (endDateVal) {
            return (
              time.getTime() < dayjs(startDateVal, 'DD/MM/YYYY').valueOf() ||
              time.getTime() > dayjs(endDateVal, 'DD/MM/YYYY').valueOf()
            );
          } else {
            return time.getTime() < dayjs(startDateVal, 'DD/MM/YYYY').valueOf();
          }
        },
      };
    },
    pickerOptionsExpireDate(data, data1) {
      return {
        disabledDate: (time) => {
          let endDateVal = data;
          let beginDateVal = data1;
          if (endDateVal) {
            let stime = new Date(endDateVal).getTime() + 24 * 60 * 60 * 1000;
            let etime = new Date(beginDateVal).getTime() + 24 * 60 * 60 * 1000;
            return stime < etime;
          } else {
            return time.getTime() < dayjs().startOf('date');
          }
        },
      };
    },
    getEffectiveDateRange(expireDate) {
      return (rule, value, callback) => {
        if (value) {
          if (expireDate && dayjs(value, 'DD/MM/YYYY').valueOf() > dayjs(expireDate, 'DD/MM/YYYY').valueOf()) {
            callback(new Error('Effective Date cannot be later than Expire Date'));
          } else {
            callback();
          }
        } else {
          callback(new Error('Please select the correct date'));
        }
      };
    },
    getExpireDateRange(effectiveDate) {
      return (rule, value, callback) => {
        if (value) {
          if (effectiveDate && dayjs(effectiveDate, 'DD/MM/YYYY').valueOf() > dayjs(value, 'DD/MM/YYYY').valueOf()) {
            callback(new Error('Effective Date cannot be later than Expire Date'));
          } else {
            callback();
          }
        } else {
          callback(new Error('Please select the correct date'));
        }
      };
    },
  },
};
</script>
