<template>
  <div class="customerInfo" :data="dataObj" border style="width: 100%">
    <div class="pro-wrp">
      <div class="basicProduct">
        <div class="basic">
          {{ $t('IDC.orgCustomer.product') }}
        </div>
        <div class="basicDec">
          {{ $t('IDC.orgCustomer.productDic') }}
        </div>
      </div>
      <el-divider />
      <div class="basicProduct1">
        <!-- 新增product按钮 -->
        <div class="pro">
          <span class="pan">*</span>
          <el-form-item
            :prop="`showproductInfoList`"
            required
            :rules="[{ validator: getProductRange, trigger: 'blur' }]"
          >
            <el-button
              v-if="dataObj.showproductInfoList.length > 0"
              @click="productEditFn()"
              plain
              size="small"
              type="primary"
            >
              <i class="el-icon-edit el-icon--right"></i>
              {{ $t('IDC.orgCustomer.edit') }}
            </el-button>
            <el-button v-else @click="productEditFn()" icon="el-icon-plus" plain size="small" type="primary">
              {{ $t('common.button.add') }}
            </el-button>
          </el-form-item>
        </div>
        <el-form-item v-if="dataObj.showproductInfoList.length > 0">
          <!-- product列表 -->
          <table-page
            :columns="selectProductColums"
            :has-pagination="true"
            :outSideTableData="dataObj.showproductInfoList"
          >
          </table-page>
        </el-form-item>
      </div>
    </div>
    <div class="segmentation"></div>
    <div class="pro-wrp">
      <div class="identityInfo">
        <div class="idTitle">
          {{ $t('IDC.orgCustomer.identityInfo') }}
        </div>
      </div>
      <el-divider />
      <div class="identityInfo">
        <div class="section-title">{{ $t('IDC.orgCustomer.basicInfo') }}</div>
        <el-row :gutter="20">
          <el-col v-bind="colOptions">
            <el-form-item
              :prop="`msisdn`"
              :label="$t('IDC.orgCustomer.MSISDN')"
              required
              class="msisdn"
              :rules="
                !upgradeMsisdn
                  ? [
                      { validator: getCheckRange, trigger: 'blur' },
                      { validator: getMsisdn, trigger: 'blur' },
                    ]
                  : []
              "
            >
              <div class="viewRange" @click="viewRangeFn">{{ $t('IDC.orgCustomer.viewRange') }}</div>
              <el-input placeholder="Please enter" v-model="dataObj.msisdn" :disabled="!!upgradeMsisdn"></el-input>
            </el-form-item>
          </el-col>
          <el-col v-bind="colOptions">
            <el-form-item prop="validityPeriod" :label="$t('IDC.orgCustomer.pegisteredBy')">
              <el-input placeholder="Please enter" v-model="dataObj.pegisteredBy" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col v-bind="colOptions">
            <el-form-item :label="$t('IDC.orgCustomer.language')">
              <el-select v-model="dataObj.languageCode" placeholder="Please select" @change="selectChannelChanged">
                <el-option v-for="item in dataObj.languageList" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col v-bind="colOptions">
            <el-form-item :prop="`invitationCode`" :label="$t('IDC.orgCustomer.invitationCode')">
              <el-input placeholder="Please enter" v-model="dataObj.invitationCode"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <!-- KYC部分 -->
      <div class="segmentation"></div>
      <div class="kyc-info">
        <div class="idTitle">
          {{ $t('KYC Info') }}
        </div>
        <div class="personalDetails" ref="kycDiv">
          <el-divider />
          <div class="kycShowDiv">
            <kyc-form
              ref="customerKycInfo"
              identityType="1002"
              identityClassify="customer"
              :kycInfo.sync="dataObj.kycFormData"
              :adaptiveLayout="true"
              mode="create"
              :basicProductId="dataObj.productInfoList.length ? dataObj.productInfoList[0].productId : ''"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- product弹框 -->
    <portal-add-popup
      id="popup-property"
      :searchVisible.sync="searchVisible"
      :hasCheckBox="false"
      :isCheck="true"
      :isCheckMemory="false"
      :isSingleCheck="true"
      :hasPagination="true"
      :addForm="servicePopupConf"
      :childSearch="loadSegments"
      @submit-call-back="onPropertyChange"
      :init-checks="initCheckList"
    >
      <template slot="trustLevel" slot-scope="scope">
        {{ `${scope.row.trustLevel}:${scope.row.trustLevelName}` }}
      </template>
    </portal-add-popup>
    <!-- View Range弹框 -->
    <el-dialog
      class="search"
      :title="$t('IDC.orgCustomer.RegisteredCustomer')"
      :visible.sync="isViewRange"
      width="800px"
      :close-on-click-modal="false"
      :before-close="cancel"
    >
      <table-page
        size="small"
        :has-pagination="false"
        :columns="columns"
        :getTableData="getTableData"
        :sortArr="['roleCode']"
      >
      </table-page>
    </el-dialog>
  </div>
</template>
<script>
import TablePage from '@/components/tablePage';
import PortalAddPopup from '@/components/portal-add-popup/index';
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
import {
  queryProduct,
  queryMsisdnRange,
  querycheckRange,
  queryCustomerIdentity,
  queryCustomerInfo,
} from '../../../../api/api-customer';

export default {
  name: 'customerInfo',
  components: { TablePage, PortalAddPopup, KycForm },
  props: {
    dataObj: {
      labels: Object,
      default() {
        return {};
      },
    },
    progressIndex: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      searchVisible: false, // dalog弹框显隐
      already: false, // 单选开关
      isAlready: 'No',
      popoverTableData: [],
      addForm: {},
      popSelectobj: {},
      radio: '1',
      columns: [
        { label: this.$t('IDC.orgCustomer.No'), prop: 'roleId', align: 'left' },
        {
          label: this.$t('IDC.orgCustomer.range'),
          prop: 'roleCode',
          'sort-method': (a, b) => {
            return a.roleCode > b.roleCode ? 1 : -1;
          },
          align: 'left',
        },
      ],
      idDetailsColumns: [
        { label: 'ID Type', prop: 'kycName', align: 'left', width: '100px' },
        { label: 'ID Number', prop: 'kycName', align: 'left', width: '100px' },
        { label: 'ID Effective Date', prop: 'effectiveTime', align: 'left', __slotName: 'effectiveDate' },
        { label: 'ID Expiry Date', prop: 'effectiveTime', align: 'left', __slotName: 'effectiveDate' },
        { label: 'Document Received', prop: 'effectiveTime', align: 'left', __slotName: 'effectiveDate' },
        { label: 'ID Status', prop: 'effectiveTime', align: 'left', __slotName: 'effectiveDate' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: 200,
          fixed: 'right',
        },
      ],
      certificateColumns: [
        { label: 'Certificate Type', prop: 'productName', align: 'left' },
        { label: 'Certificate Number', prop: 'effectiveDate', align: 'left', __slotName: 'effectiveDate' },
        { label: 'Certificate Certified Date', prop: 'expiryDate', align: 'left', __slotName: 'expiryDate' },
        { label: 'Certificate Expiry Date', prop: 'chargeProfile', align: 'left', __slotName: 'chargeProfile' },
        { label: 'Certificate Status', prop: 'chargeProfile', align: 'left', __slotName: 'chargeProfile' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        },
      ],
      organizationColumns: [
        { label: 'Contact Type', prop: 'productName', align: 'left' },
        { label: 'Certificate Number', prop: 'effectiveDate', align: 'left', __slotName: 'effectiveDate' },
        { label: 'Contact Second Name', prop: 'expiryDate', align: 'left', __slotName: 'expiryDate' },
        { label: 'Contact Surname', prop: 'chargeProfile', align: 'left', __slotName: 'chargeProfile' },
        { label: 'Contact Phone Number', prop: 'chargeProfile', align: 'left', __slotName: 'chargeProfile' },
        { label: ' Contact Status', prop: 'chargeProfile', align: 'left', __slotName: 'chargeProfile' },
        {
          label: this.$t('common.label.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          fixed: 'right',
        },
      ],
      selectProductColums: [
        { label: this.$t('IDC.orgCustomer.productName'), prop: 'productName', align: 'left' },
        { label: this.$t('IDC.orgCustomer.trustLevel'), prop: 'trustLevel', align: 'left' },
        { label: this.$t('IDC.orgCustomer.description'), prop: 'productDesc', align: 'left' },
      ],
      isViewRange: false, // 展示ViewRange弹框
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 5,
      },
      servicePopupConf: {
        title: 'Add Product',
        label: 'Product Name',
        idKey: 'productId',
        columns: [
          { label: 'Product Name', prop: 'productName', align: 'left' },
          { label: 'Trust Level', prop: 'trustLevelName', align: 'left', __slotName: 'trustLevel' },
          { label: 'Description', prop: 'productDesc', align: 'left' },
        ],
        searchForm: {
          formData: {
            name: null,
          },
          formConf: [
            {
              label: 'Product Name',
              key: 'productName',
              prop: 'productName',
              comp: 'el-input',
              bind: {
                clearable: true,
                placeholder: this.$t('common.placeholder.enter'),
              },
            },
          ],
        },
      },
      initCheckList: [],
      routeQueryData: {},
    };
  },
  computed: {
    upgradeMsisdn() {
      return this.routeQueryData.upgradeMsisdn || '';
    },
  },
  created() {
    let params = this.$route.query.params;
    this.routeQueryData = (params && JSON.parse(decodeURIComponent(params))) || {};
  },
  methods: {
    // 新增product事件
    productEditFn() {
      this.searchVisible = true;
      this.addForm = {
        title: this.$t('IDC.orgCustomer.addProduct'),
        label: [this.$t('IDC.orgCustomer.productName')],
        columns: [
          { label: this.$t('IDC.orgCustomer.productName'), prop: 'productName', align: 'left' },
          { label: this.$t('IDC.orgCustomer.trustLevel'), prop: 'trustLevel', align: 'left' },
          { label: this.$t('IDC.orgCustomer.description'), prop: 'description', align: 'left' },
        ],
        // 表单数据
        selItem: {
          propertyName: '',
        },
      };
    },
    selectChannelChanged(data) {
      let obj = this.dataObj.languageList.find((item) => {
        return item.id === data;
      });
      this.dataObj.languageName = obj.name;
    },
    viewRangeFn() {
      this.isViewRange = true;
    },
    cancel() {
      this.isViewRange = false;
    },
    getTableData() {
      return new Promise((resolve) => {
        queryMsisdnRange()
          .then((res) => {
            if (res.header && res.header.success) {
              let list = [];
              res.msisdnRanges.forEach((item, index) => {
                let obj = {
                  roleId: ++index,
                  roleCode: item,
                };
                list.push(obj);
              });
              res.list = list;
            }

            resolve(res);
          })
          .catch(() => {})
          .finally(() => {
            this.searchLoading = false;
            this.isSearch = false;
          });
      });
    },

    loadSegments(param) {
      return new Promise((resolve) => {
        let params = {
          page: param.pageNo || 1,
          pageSize: param.pageSize || 5,
          productName: param.searchKey,
        };
        if (param.searchKey) {
          params.productName = param.searchKey.productName;
        }
        queryProduct(params)
          .then((res) => {
            if (res.header && res.header.success) {
              res.list = res.products;
              res.total = res.page.totalSize;
            }
            resolve(res);
          })
          .catch(() => {})
          .finally(() => {
            this.searchLoading = false;
            this.isSearch = false;
          });
      });
    },
    onPropertyChange(data) {
      if (!data) return;
      this.initCheckList = [];
      this.initCheckList.push(data);
      this.$emit('set-my-product', { content: data });
    },
    getCheckRange(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the number'));
      } else {
        let params = {
          msisdn: value,
        };
        querycheckRange(params).then((res) => {
          if (res.header && res.header.success) {
            if (!res.result) {
              return callback(new Error('The phone number does not meet the requirements'));
            } else {
              return callback();
            }
          } else {
            return callback(new Error('The phone number does not meet the requirements'));
          }
        });
      }
    },
    getMsisdn(rule, value, callback) {
      if (!value) {
        callback(new Error('Please enter the number'));
      } else {
        let params = {
          msisdn: value,
        };
        queryCustomerIdentity(params).then((res) => {
          if (res.header && res.header.success) {
            let obj = res.identityId;
            if (obj && obj.length > 0) {
              queryCustomerInfo({ customerId: obj })
                .then((res) => {
                  if (res.header && res.header.success === true) {
                    let customerType = res.customerType;
                    if (customerType === '1000') {
                      return callback();
                    } else {
                      return callback(new Error('The MSISDN has been used!'));
                    }
                  } else {
                    return callback(new Error('The MSISDN has been used!'));
                  }
                })
                .catch(() => {
                  return callback(new Error('The MSISDN has been used!'));
                });
            } else {
              return callback();
            }
          } else {
            return callback(new Error('The phone number does not meet the requirements'));
          }
        });
      }
    },
    getProductRange(rule, value, callback) {
      if (value.length === 0) {
        callback(new Error('Please add'));
      } else {
        callback();
      }
    },
    checkKYC() {
      this.$refs.customerKycInfo.validate((valid) => {
        this.$emit('customerkycinfo-validate', { content: valid });
      });
    },
    getCustomerkycinfoDataEvn() {
      return this.$refs.customerKycInfo.getKYCRequestParams();
    },
  },
};
</script>
