<template>
  <div class="steps-tabs-container">
    <el-tabs v-model="tabsActiveName" class="info-tabs-container" style="margin-top: 20px; pointer-events: none">
      <el-tab-pane v-for="(item, index) in tabsPanel" :key="index" :name="item.name" style="pointer-events: auto">
        <div slot="label">
          <div
            class="steps-container"
            :class="{ 'complete-steps-container': getItemStatus(item, index) === 'COMPLETED' }"
            v-if="getItemStatus(item, index) === 'COMPLETED'"
          >
            <i class="el-icon-a-create-successsmall success-icon" />
          </div>
          <div
            class="steps-container"
            :class="{ 'active-steps-container': getItemStatus(item, index) === 'ACTIVE' }"
            v-if="getItemStatus(item, index) !== 'COMPLETED'"
          >
            {{ index + 1 }}
          </div>
          <div class="steps-title" :class="{ 'active-steps-title': getItemStatus(item, index) === 'ACTIVE' }">
            {{ item.title }}
          </div>
          <div class="steps-sub-title">{{ item.subTitle }}</div>
        </div>
        <slot :name="item.name"></slot>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: 'StepsTabs',
  props: {
    activeName: {
      type: String,
      default: 'first',
    },
    tabsPanel: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      tabsActiveName: '',
    };
  },
  computed: {
    getItemStatus() {
      const activeName = this.activeName;
      return function (item, index) {
        const activeIndex = this.tabsPanel.findIndex((tab) => tab.name === activeName);
        if (index === activeIndex) {
          return 'ACTIVE';
        } else if (index < activeIndex) {
          return 'COMPLETED';
        } else {
          return 'INACTIVE';
        }
      };
    },
  },
  watch: {
    activeName() {
      this.tabsActiveName = this.activeName;
    },
  },
  created() {
    this.tabsActiveName = this.activeName;
  },
};
</script>

<style lang="scss" scoped>
.steps-tabs-container {
  /deep/ .el-tabs__item {
    height: auto !important;
    text-align: center;
    color: $--text_color--strong;
    font-weight: regular;
    font-size: 12px;
    line-height: 20px;
    padding: 0 30px;
    cursor: default;
  }
  /deep/ .el-tabs__nav {
    display: flex;
    width: 100%;
  }
  .success-icon {
    color: $--primary_color;
    font-size: 26px;
  }
}
</style>
