<template>
  <div class="customer-create-config create-page-content">
    <div v-if="!createCompleted">
      <!-- header -->
      <portal-header :projectObj="titleConfig"></portal-header>
      <!-- stepTab -->
      <div class="step-tabs-outer-wrapper">
        <el-form
          label-width="100px"
          label-position="top"
          :rules="queryFormRules"
          :model="customerModel"
          ref="customerRuleForm"
          class="add-form"
        >
          <steps-tabs :activeName="activeName" :tabs-panel="tabsPanel">
            <template slot="first">
              <customer-Info
                ref="CustomerInfo"
                :dataObj.sync="customerModel"
                @set-my-product="setMyProductEvn"
                @customerkycinfo-validate="CustomerKycInfoValidate"
                :progressIndex="progressIndex"
              ></customer-Info>
            </template>
            <template slot="second">
              <customer-details
                ref="CustomerDetails"
                :dataObj.sync="customerModel"
                @charge-profile-usedefault="chargeprofileusedefaultEvn"
                @rule-profile-usedefault="ruleprofileusedefaultEvn"
                @refresh-accountType="refreshAccountTypeEvn"
                :crIndex.sync="progressIndex"
                :detaileLoading="detaileLoading"
              ></customer-details>
            </template>
            <template slot="third">
              <customer-review :dataObj.sync="customerModel"></customer-review>
            </template>
          </steps-tabs>
        </el-form>
      </div>
      <footer-tap
        @reset="cancelEvn"
        @previous="previousEvn"
        @next="submitEvn"
        @submit="submitEvn"
        :active-name="activeName"
        :steps="tabsPanel"
        :submit-loading="submitLoading"
      />
    </div>

    <portal-create-success
      v-else
      :successText="successText"
      detailText="View Detail >>"
      :addText="addjmuText"
      @view-detail="goViewDetail"
      @continue-add="goContinueToAdd"
    >
    </portal-create-success>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import PortalCreateSuccess from '@/components/portal-create-success';
import StepsTabs from '@/components/stepsTabs';
import customerInfo from './components/customer-Info';
import customerReview from './components/customer-review';
import CustomerDetails from './components/customer-details.vue';
import footerTap from '@/components/portal-footer-tap';
import { getDataFormRules } from '../../../validator/customer.js';
import cookie from '@/common/utils/cookie';
import {
  queryLanguage,
  // queryChargeProfileList,
  // queryRuleProfileList,
  queryAccountType,
  queryaddCustomer,
  querytrustLevelAndSegment,
  checkInputKyc,
  upgradeCustomer,
} from '../../../api/api-customer';
export default {
  components: {
    StepsTabs,
    PortalCreateSuccess,
    PortalHeader,
    customerInfo,
    customerReview,
    CustomerDetails,
    footerTap,
  },
  name: 'GroupcreateConfiguration',
  data() {
    return {
      identityId: '',
      createCompleted: false,
      successText: 'Operation succeeded.',
      addjmuText: 'Continue to Add >>',
      activeName: 'first', // 选项卡
      buttonText: 'Next',
      progressIndex: 0,
      tabsPanel: [
        {
          name: 'first',
          title: this.$t('IDC.orgCustomer.identityInfo'),
          subTitle: this.$t('IDC.orgCustomer.identityInfoSub'),
        },
        {
          name: 'second',
          title: this.$t('IDC.orgCustomer.enterDetails'),
          subTitle: this.$t('IDC.orgCustomer.enterDetailsSub'),
        },
        {
          name: 'third',
          title: this.$t('IDC.orgCustomer.review'),
          subTitle: this.$t('IDC.orgCustomer.reviewSub'),
        },
      ],
      queryFormRules: getDataFormRules(),
      // 数据模型

      customerModel: {
        productInfoList: [],
        showproductInfoList: [],
        msisdn: '',
        pegisteredBy: cookie.get('operator_name') || '',
        languageName: '',
        languageCode: '',
        languageList: [],
        invitationCode: '',
        selectChargeProfileList: [],
        selectRuleProfileList: [],
        chargeProfileobj: {},
        ruleProfileobj: {},
        accountTypeList: [],
        kycFormData: {},
        selectSegmentName: '',
        trustLevel: '',
        trustLevelId: '',
        trustLevelName: '',
        segmentIds: '',
      },
      uploadDataModel: {
        customerCreateBasicInfo: {
          invitationCode: '',
          languageCode: '',
          msisdn: '',
          registeredBy: '',
        },
        identityChargeProfileId: '',
        identityRuleProfileId: '',
        identityKYCData: {},
        products: [],
        accountTypeRuleProfileBeanList: [],
      },

      defaultChargeProfileId: '',
      defaultRuleProfileId: '',
      detaileLoading: false,
      submitLoading: false,
      routeQueryData: {},
    };
  },
  computed: {
    titleConfig() {
      return {
        title: this.$t(!this.upgradeMsisdn ? 'IDC.orgCustomer.createCustomer' : 'IDC.orgCustomer.upgradeTitle'),
        desc: this.$t(!this.upgradeMsisdn ? 'IDC.orgCustomer.dic' : 'IDC.orgCustomer.upgradeDesc'),
      };
    },
    basePath() {
      return `mainPage/identifierCenter/organizationInfo/customer/`;
    },
    upgradeMsisdn() {
      return this.routeQueryData.upgradeMsisdn || '';
    },
  },
  created() {
    this.loadQueryLanguage();
    // this.loadQueryChargeProfileList();
    // this.loadQueryRuleProfileList();
    let params = this.$route.query.params;
    this.routeQueryData = (params && JSON.parse(decodeURIComponent(params))) || {};
    if (this.upgradeMsisdn) {
      this.customerModel.msisdn = this.upgradeMsisdn;
    }
  },
  activated() {},
  methods: {
    cancelEvn() {
      this.$confirm('Are you sure to reset all data you have entered/selected in this page?', 'Reset', {
        confirmButtonText: this.$t('common.button.confirm'),
        cancelButtonText: this.$t('common.button.cancel'),
        closeOnClickModal: false,
        type: 'warning',
      }).then(() => {
        this.refreshCurrentPage();
      });
    },
    previousEvn() {
      if (this.progressIndex == 1) {
        this.customerModel.productInfoList = [];
        let obj = Object.assign({}, this.customerModel.showproductInfoList[0]);
        this.customerModel.productInfoList.push(obj);
      }
      this.changeProgressIndex(false);
    },
    submitEvn() {
      if (this.progressIndex == 2) {
        this.createCustomer();
      } else if (this.progressIndex == 0) {
        this.$refs.customerRuleForm.validate((valid) => {
          if (valid) {
            this.$refs.CustomerInfo.checkKYC();
          }
        });
      } else if (this.progressIndex == 1) {
        this.$refs.CustomerDetails.validate().then((valid) => {
          if (valid) {
            this.changeProgressIndex(true);
          }
        });
      }
    },
    changeProgressIndex(step) {
      if (step) {
        if (this.progressIndex < 2) {
          this.progressIndex++;
        }
      } else {
        if (this.progressIndex > 0) {
          this.progressIndex--;
        }
      }
      this.mapProgressIndex();
    },
    mapProgressIndex() {
      if (this.progressIndex == 0) {
        this.activeName = 'first';
        this.buttonText = 'Next';
      } else if (this.progressIndex == 1) {
        this.activeName = 'second';
        this.buttonText = 'Next';
      } else if (this.progressIndex == 2) {
        this.activeName = 'third';
        this.buttonText = 'Submit';
      }
    },
    refreshAccountTypeEvn() {
      this.loadQueryAccountType();
    },
    ruleprofileusedefaultEvn() {
      // this.loadQueryDefaultRuleProfile();
      this.customerModel.ruleProfileobj = {
        ...this.customerModel.selectRuleProfileList.filter(
          (item) => item.ruleProfileId == this.defaultRuleProfileId
        )[0],
      };
    },
    chargeprofileusedefaultEvn() {
      // this.loadQueryDefaultChargeProfile();
      this.customerModel.chargeProfileobj = {
        ...this.customerModel.selectChargeProfileList.filter(
          (item) => item.chargeProfileId == this.defaultChargeProfileId
        )[0],
      };
    },
    // ====successEvn======
    goViewDetail() {
      let data = {
        identityID: this.identityId,
      };
      this.$router.push({
        path: 'detail',
        query: { params: encodeURIComponent(JSON.stringify(data)) },
      });
    },
    goContinueToAdd() {
      this.refreshCurrentPage();
    },
    cancelFn(closeDialog) {
      if (!closeDialog) {
        this.$confirm('Are you sure to cancel the operation?', 'Cancel', {
          confirmButtonText: this.$t('common.button.confirm'),
          cancelButtonText: this.$t('common.button.cancel'),
          closeOnClickModal: false,
          type: 'warning',
        }).then(() => {
          this.$tagTab.closeCurrentPage();
        });
      }
    },
    packageUploadData() {
      this.uploadDataModel.customerCreateBasicInfo.invitationCode = this.customerModel.invitationCode;
      this.uploadDataModel.customerCreateBasicInfo.languageCode = this.customerModel.languageCode;
      this.uploadDataModel.customerCreateBasicInfo.msisdn = this.customerModel.msisdn;
      // this.uploadDataModel.customerCreateBasicInfo.registeredBy = this.customerModel.registeredBy;
      this.uploadDataModel.identityChargeProfileId = '' + (this.customerModel.chargeProfileobj.chargeProfileId || '');
      this.uploadDataModel.identityRuleProfileId = '' + (this.customerModel.ruleProfileobj.ruleProfileId || '');
      this.uploadDataModel.products = this.customerModel.productInfoList;
      this.uploadDataModel.accountTypeRuleProfileBeanList = this.customerModel.accountTypeList;
      this.uploadDataModel.identityKYCData = this.$refs.CustomerInfo.getCustomerkycinfoDataEvn();
      this.uploadDataModel.trustLevel = this.customerModel.trustLevel;
      this.uploadDataModel.trustLevelId = this.customerModel.trustLevelId;
      this.uploadDataModel.segmentIds = this.customerModel.segmentIds;
      this.$set(this.uploadDataModel, 'requestId', new Date().getTime());
    },

    createCustomer() {
      this.packageUploadData();
      if (this.upgradeMsisdn) {
        this.submitLoading = true;
        this.$set(this.uploadDataModel, 'customerId', this.routeQueryData.identityId);
        upgradeCustomer(this.uploadDataModel)
          .then((data) => {
            if (data.header && data.header.success) {
              this.changeProgressIndex(true);
              this.createCompleted = true;
              this.identityId = data.identityId;
            } else {
            }
          })
          .finally(() => {
            this.submitLoading = false;
          });
      } else {
        this.submitLoading = true;
        queryaddCustomer(this.uploadDataModel)
          .then((data) => {
            if (data.header && data.header.success) {
              this.changeProgressIndex(true);
              this.createCompleted = true;
              this.identityId = data.identityId;
            } else {
            }
          })
          .finally(() => {
            this.submitLoading = false;
          });
      }
    },
    loadQueryLanguage() {
      queryLanguage().then((data) => {
        this.customerModel.languageList = data.locales;
      });
    },
    loadQueryAccountType() {
      if (this.customerModel.productInfoList.length > 0) {
        let productIds = [];
        this.customerModel.accountTypeList = [];
        this.customerModel.productInfoList.forEach((item) => {
          productIds.push(item.productId);
        });
        let params = {
          productIds: productIds,
          trustLevelId: this.customerModel.trustLevelId,
          segmentIds: this.customerModel.segmentIds,
        };
        queryAccountType(params).then((data) => {
          data.accountTypeRuleProfileBeanList.forEach((item) => {
            item.ruleid = item.ruleProfileId;
            item.rule = item.ruleProfileName;
            this.customerModel.accountTypeList.push(item);
          });
        });
      }
    },
    loadQueryTrustLevelAndSegment() {
      let obj = this.customerModel.productInfoList.find((item) => {
        return item.isProduct === 0;
      });
      let params = {
        selectBasicProductId: obj.productId,
        identityKYCData: this.$refs.CustomerInfo.getCustomerkycinfoDataEvn(),
      };
      this.detaileLoading = true;
      querytrustLevelAndSegment(params)
        .then((data) => {
          this.detaileLoading = false;
          if (data.header && data.header.success) {
            this.customerModel.selectChargeProfileList = data.chargeProfileList || [];
            this.customerModel.chargeProfileobj = {
              ...(data.chargeProfileList || []).filter((item) => item.chargeProfileId == data.selectChargeProfileId)[0],
            };
            this.customerModel.selectRuleProfileList = data.ruleProfileList || [];
            this.customerModel.ruleProfileobj = {
              ...(data.ruleProfileList || []).filter((item) => item.ruleProfileId == data.selectedRuleProfileId)[0],
            };
            this.defaultChargeProfileId = data.defaultChargeProfileId;
            this.defaultRuleProfileId = data.defaultRuleProfileId;
            this.customerModel.selectSegmentName = data.selectSegmentName;
            this.customerModel.trustLevel = data.trustLevel;
            this.customerModel.trustLevelId = data.trustLevelId;
            this.customerModel.trustLevelName = data.trustLevelName;
            this.customerModel.accountTypeList = data.accountTypeRuleProfileBeanList.map((item) => {
              if (!item.ruleProfileId) {
                item.ruleProfileId = item.defaultRuleProfileId;
              }
              return { ...item };
            });
            this.customerModel.segmentIds = data.segmentIds;
            this.customerModel.productInfoList = [];
            data.products.forEach((item) => {
              let obj = Object.assign({}, item);
              obj.chargeProfileId = obj.chargeProfileId || obj.defaultChargProfileId || '';
              obj.chargeProfiles = obj.chargeProfiles || [];
              obj.effectiveDateInit = obj.effectiveDate;
              obj.expireDateInit = obj.expireDate;
              if (item.productId != this.customerModel.showproductInfoList[0].productId) {
                obj.isProduct = 2;
                this.customerModel.productInfoList.push(obj);
              } else {
                obj.isProduct = 0;
                this.customerModel.productInfoList.unshift(obj);
              }
            });
          }
        })
        .finally(() => {
          this.detaileLoading = false;
        });
    },

    setMyProductEvn(data) {
      let obj = Object.assign({}, data.content);
      obj.isProduct = 0;
      obj.chargeProfileId = obj.chargeProfileId || obj.defaultChargProfileId || '';
      obj.chargeProfiles = obj.chargeProfiles || [];
      if (this.customerModel.productInfoList.length == 0) {
        this.customerModel.productInfoList.push(obj);
        this.customerModel.showproductInfoList.push(obj);
      } else {
        this.$set(this.customerModel.showproductInfoList, 0, obj);
        this.customerModel.productInfoList.forEach((item, index) => {
          if (item.isProduct === 0) {
            this.$set(this.customerModel.productInfoList, index, obj);
            return;
          }
        });
      }
    },
    CustomerKycInfoValidate(data) {
      if (data.content) {
        let param = {
          identityKYCData: this.$refs.CustomerInfo.getCustomerkycinfoDataEvn(),
          trustLevelId: this.customerModel.productInfoList[0].trustLevelId,
        };
        this.submitLoading = true;
        checkInputKyc(param)
          .then((data) => {
            if (data.result) {
              this.loadQueryTrustLevelAndSegment();
              this.changeProgressIndex(true);
            }
          })
          .finally(() => {
            this.submitLoading = false;
          });
      }
    },
  },
};
</script>
<style scoped lang="scss">
