  background-color: $--text_color--white;
  box-sizing: border-box;
  min-height: 100%;
  border-radius: 4px;

  /deep/.el-tabs__header {
    padding: 0px 20px 36px 20px;
    margin: 0px;
  }

  /deep/.el-tabs__nav-wrap {
    padding-left: 20px;
  }

  /deep/.el-tabs__header::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 16px;
    background-color: $--global-background_color;
    z-index: 1;
  }

  /deep/ .el-tabs__nav-wrap {
    overflow: hidden;
    margin-bottom: -1px;
    position: relative;
  }

  /deep/.el-tabs__nav-wrap::after {
    background-color: $--fill_color--regular;
  }

  /deep/.el-input__inner {
    font-size: 14px;
  }

  /deep/ .el-dialog__footer {
    margin: 20px 0px 0 !important;
